<template>
  <div class="snsApp">
    <v-btn
    position="absolute"
    >+</v-btn>
    <v-responsive
      class="overflow-y-auto"
    >

      <v-responsive
        class="text-center pa-2"
      >
        <v-responsive min-height="50vh"></v-responsive>
        <div class="text-center text-body-2 mb-12">
          The card will appear below:
        </div>

        <v-lazy
          v-model="isActive"
          :options="{
            threshold: .5
          }"
          min-height="200"
          transition="fade-transition"
          v-for="(e,i) in cardArray" :key="i"
        >
          <v-card
            class="mx-auto text-left"
            max-width="100vh"
          >
            <v-card-title>{{e.user}}</v-card-title>
            <v-card-text>{{e.contents}}</v-card-text>
            <v-card-subtitle
            class="text-right dateDay"
            >{{e.date}}</v-card-subtitle>
          </v-card>
        </v-lazy>
      </v-responsive>
    </v-responsive>

  </div>
</template>
<script>
  export default {
    name: 'SnsView',
    data () {
      return {
        s: 1,
        cardArray: [],
      }
    },
    methods: {
    },
    mounted () {
      this.$axios.post('/api/notice', { ok: 'ok' }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          console.log(res.data[i])
          this.cardArray.unshift(res.data[i])
        }
      })
    },
  }
</script>
<style lang="css">
  .dateDay{
    color: gray;
  }
  v-card{
    margin-bottom: 300px;
  }
</style>
